$avatar-size: 32px;
$body-background: #333641;

@font-face {
	font-family: "ubuntu";
	font-style: italic;
	font-weight: 300;
	src: local("Lato Light Italic"), local("Lato-LightItalic"),
		url(https://fonts.gstatic.com/s/ubuntucondensed/v8/u-4k0rCzjgs5J7oXnJcM_0kACGMtT-Dfqw.woff2)
			format("woff2");
}

body {
	height: 100vh;
	margin: 0;
	background-color: $body-background;
	font-family: "ubuntu", Arial, sans-serif;
	overflow-x: hidden;
	display: grid;
	place-items: center;
}

a {
	text-decoration: none;
	color: #9ca0b1;
}

.wrapper {
	text-align: center;
    width: 60%;
    margin-top: 5em;
    margin-left: -3em;
}
.wrapper h1 {
    color: #fff;
    font-size: 3em;
    /* font-family: "ubuntu"; */
    /* text-decoration: underline; */
    /* text-transform: uppercase; */
    font-weight: 700;
    /* font-family: "Josefin Sans", sans-serif; */
    background: -webkit-gradient(linear, left top, right top, color-stop(10%, #0f52ba), color-stop(50%, #1034a6), color-stop(60%, #000080));
    background: linear-gradient(to right, #0f52ba 10%, #1034a6 50%, #000080 60%);
    background-size: auto auto;
    background-clip: border-box;
    background-size: 200% auto;
    color: #fff;
    background-clip: text;
    /* text-fill-color: transparent; */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-animation: textclip 3s linear infinite;
            animation: textclip 3s linear infinite;
    display: inline-block;
}

@-webkit-keyframes textclip {
	to {
		background-position: 200% center;
	}
}

@keyframes textclip {
	to {
		background-position: 200% center;
	}
}

.vision-page {
    background: midnightblue;
    color: #fff;
    padding: 5em 2em;
    text-align: center;
    height: auto;
}
.vision-page h1 {
    font-size: 3.5em;
    padding-bottom: .5em;
    letter-spacing: 4px;
}
.vision-page p {
    font-size: 2em;
    letter-spacing: 1px;
    margin-bottom: 1.5em;
}
.vision-page a {
   font-size: 1.5em;
   text-decoration: underline;
   color: #fff;
   font-weight: bold;
   letter-spacing: 1px;
}
.events-body {
 height: auto;
 overflow: hidden;
 background: -webkit-gradient(linear,left top, left bottom,from(midnightblue),to(royalblue));
 background: linear-gradient(midnightblue,royalblue);
 opacity: .9;
}
.events-body img {
    position: absolute;
    z-index: -1;
    width: 100%;
    opacity: .5;
}
.events-box {
    display: block;
    z-index: 1;
    border: none;
    background: red;
    color: #fff;
    font-weight: bold;
    padding: 1em;
    font-size: 2em;
    width: 25%;
    margin: 1em;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5),0 6px 20px 0 rgba(0,0,0,0.5);
    cursor: pointer;
}
.events-box::-moz-selection {
    background: none;
}
.events-box::selection {
    background: none;
}
.content {
    display: none;
    position: relative;
    float: right;
    width: 60%;
    margin-top: -34.7em;
    margin-right: 1.8em;
    background: #fff;
    padding: 2em 1.5em;
    -webkit-animation: .3s AnimateDiv;
            animation: .3s AnimateDiv;
    font-size: 1.2em;
}
.block-display {
    display: block;
}
@-webkit-keyframes AnimateDiv {
    from {
        top: -10em;
        opacity: 0;
    }
    to {    
        top: 0;
        opacity: 1;
    }
}
@keyframes AnimateDiv {
    from {
        top: -10em;
        opacity: 0;
    }
    to {    
        top: 0;
        opacity: 1;
    }
}

.donationContainer {
